/* eslint-disable require-jsdoc */
import React, { Component } from 'react';

import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox, { CheckboxProps } from '@material-ui/core/Checkbox';
import LinkItem from '../../../components/linkItem';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';


import Header from '../../../components/header';
import Sidebar from '../../../components/sidebar';
import Button from '@material-ui/core/Button';
import Loader from '../../../components/loader';
import Snackbar from '../../../components/snackbar';
import axios from "axios";
import CircularProgress from '@material-ui/core/CircularProgress';

import { withTheme } from 'react-jsonschema-form';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Modal from "react-responsive-modal";

import { Theme as MuiTheme } from 'rjsf-material-ui';
import Logo from  '../../../images/ind-logo.png';

const Form = withTheme(MuiTheme);
const schema = require('./schema.json');
const uiSchema = require('./ui-schema.json');
const formData = require('./formData.json');




class register extends Component {
  constructor(props) {
    super(props);
    this.state = {
      schema: schema,
      uiSchema: uiSchema,
      formData: formData,
      loading: false,
      getLoansLoader: false,
      open: false,
      message:'',
      openModal: true,
    };
  }

  onChange = (value) => {
    console.log('value', value)
  };

  onBeforeChange = (value) => {
    const { onChange } = this.props;
    console.log('value', value)
  };

  onSubmit = (value) => {
    console.log('onSubmit:', value.formData)
    let data = value.formData
    console.log('onSubmit data:', data)

     this.registerUser(data)

  }


  onOpenModal = () => {
    this.setState({ openModal: true });
  };

  onCloseModal = () => {
    this.setState({ openModal: false });
  };



    async registerUser(data) {
    this.setState({ getLoansLoader: true });
   
    try {
      const res = await axios.post(process.env.react_app_base_url+'/credit/user_register', data);
      const resp = res.data;
      console.log("Registraion", resp);
      if (res.status === 200) {
        
        this.setState({ getLoansLoader: false, open: true, message: "Please Check Your Email To Activate Your Account!" });

        setTimeout(
          function() {
            this.props.history.push('/');
          }
              .bind(this),
          2000,
      );

      }
      else if (res.status === 201) {

        this.setState({ getLoansLoader: false, open: true, message:resp.message });

        setTimeout(
          function() {
            window.location.reload(false);
          }
              .bind(this),
          2000,
      );

      }
      else {
        alert("Failed");
        
      }
    } catch (error) {

      if (error.message === 'Network Error') {
        alert(JSON.stringify(error.message));
        this.setState({open: true, message: error.message});
        setTimeout(
            function() {
              window.location.reload(false);
            }
                .bind(this),
            2000,
        );
      } else {
        console.log("error", JSON.stringify(error))
        alert('Something went wrong please try again');
        setTimeout(
          function() {
            window.location.reload(false);
          }
              .bind(this),
          2000,
      );
      }

    }
  };

  onCancel = () => {
    const { data } = this.props;
    this.setState({
      ...data,
    });
  }

  async componentDidMount() {
    this.setState({ loading: true });
  };


  render() {
    const { schema, uiSchema, formData } = this.state;

    return (
      <React.Fragment>
         
        <div className="page" id="site-login">
         
          <div className="logo">
            <img alt={'IN-D Credit'} src={Logo} ></img>
          </div>

<div className="login-body">


              {this.state.loading === true ? <Form
                schema={this.state.schema}
                uiSchema={uiSchema}
                formData={formData}
                onSubmit={this.onSubmit}
                onChange={this.onFormChanged}
                // theme={theme}
              >
                <div className="navbarSteps">
                  <div className="row justify-content-end">
                  <div className="col-md-12">

                    <Button variant="contained" color="primary" type="submit" disabled={this.state.getLoansLoader === true ? true : false} >
                      {this.state.getLoansLoader === true ? <CircularProgress size="20px" color="secondary" /> : ''}

                      Register  </Button>

                      

                    <div className="loginCheckbox">
                      <p className="lineDivider">
                        <span>Have IN-D Account?</span>

                      </p>


                      <LinkItem to={'/'} variant="contained" title={'Login'}> </LinkItem>
                    </div>


                  </div>
                </div>
                </div>
              </Form> : <Loader msg={"Please wait..."} />}

              {this.state.open === true ? <React.Fragment><Snackbar msg={this.state.message} open="true" /> </React.Fragment> : ' '}


            {/* <Modal className="registerPopup" open={this.state.openModal} onClose={this.onCloseModal}>
              
          <h3>{this.state.message}</h3>
        </Modal> */}


              </div>
        </div>
       
      </React.Fragment>
    );
  }
}

export default register;