/* eslint-disable require-jsdoc */
import React, {Component} from 'react';
import Button from '@material-ui/core/Button';
import {NavLink} from 'react-router-dom';

export default class linkItem extends Component {
  // constructor(props) {
  //     super(props);
  //     this.state = {};
  // }
  render() {
    return (
      <NavLink className={'nav-link ' + this.props.className} to={this.props.to}>
        <Button aria-controls="simple-menu" aria-haspopup="true">
          {this.props.icon === undefined? ' ' : <span className="icon">
            <img alt={this.props.title} title={this.props.title} src={process.env.PUBLIC_URL + '/' + this.props.icon} />
          </span>}
          {this.props.step === undefined ? ' ' : <span className="step">
            {this.props.step}
          </span>}
          <span className="text">
            {this.props.title}
          </span>
          {/* { ...children} */}
        </Button>
      </NavLink>
    );
  }
}
