/* eslint-disable require-jsdoc */
import React, { Component } from 'react';
import { PredefinedMenu as MenuJSON } from './menu';
import LinkItem from '../linkItem';
import Logo from '../../images/intain-logo.png';
import Button from '@material-ui/core/Button';
import axios from "axios";
import Loader from '../../components/loader';
import Snackbar from '../../components/snackbar';

import { Progress } from 'reactstrap';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { withRouter } from "react-router";



class sidebar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentUser: sessionStorage.getItem('name'),
      loading: false,
      getLoansLoader: false,
      open: false,
      message: '',
    };
  }




  logoutBtn = () => {
    const data  = {'token':  sessionStorage.getItem('reactToken')};
    this.logoutAPI(data);
  }

  async logoutAPI(data) {
    this.setState({ getLoansLoader: true });

    try {
      const res = await axios.post(process.env.react_app_base_url + '/credit/logout', data);
      const resp = res.data;
      console.log("resp", resp);



      if (res.status === 200) {
        toast.success(res.data.message);
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );

      } else if (res.status === 201) {


        if (res.data.message == 'Token is invalid' || res.data.message == 'Not Authorized' || res.data.message == 'Token is missing!') {

          toast.error(res.data.message);
          setTimeout(
            function () {
              this.props.history.push('/');
            }
              .bind(this),
            2000,
          );

        }

      }
      else {


        toast.error(res.data.message);

        setTimeout(
          function () {
            window.location.reload(false);
          }
            .bind(this),
          2000,
        );


      }

        

    } catch (error) {

     
        toast.error('Network Error');
        if (error.message === 'Network Error') {
          alert(JSON.stringify(error.message));
          this.setState({ open: true, message: error.message });
          setTimeout(
            function () {
              window.location.reload(false);
            }
              .bind(this),
            2000,
          );
        } else {
          console.log("error", JSON.stringify(error))
          alert('Something went wrong please try again');
          setTimeout(
            function () {
              window.location.reload(false);
            }
              .bind(this),
            2000,
          );
        }


    }
  }

  async componentDidMount() {
    // currentUser: sessionStorage.getItem('name').charAt(0).toUpperCase(),

    console.log("this.state.currentUser", this.state.currentUser)

    if(this.state.currentUser !== null){
      const firstname = this.state.currentUser;
      const namefirstletter =  firstname.charAt(0).toUpperCase();
      this.setState({currentUser:namefirstletter});
    }
    
  };

  render() {
    return (
      <React.Fragment>
        <div className="sidebar">
          <ul>
            <li className="logoPlaceholder"><LinkItem to={'/dashboard'} title={''} icon={'logo-ind.png'}> </LinkItem></li>
            {MenuJSON.map((item) => {
              return (
                <li key={item.title}>
                  <LinkItem to={item.linkto} title={item.title} icon={item.icon}></LinkItem>
                  {item.subitems != null ? (
                    <React.Fragment>
                      <ul>
                        {item.subitems.map((item1) => {
                          return (
                            <li key={item1.title}>
                              <LinkItem to={item1.linkto} title={item1.title}></LinkItem>
                            </li>
                          );
                        })
                        }
                      </ul>
                    </React.Fragment>
                  ) : (
                      <React.Fragment> </React.Fragment>
                    )
                  }
                </li>
              );
            })
            }
          </ul>
          <ul className="bottomMenu">
            <li className="userInfo"><LinkItem to={'/dashboard'} title={this.state.currentUser} > </LinkItem></li>
            <li className="showMenu"> <Button className="logout" variant="contained" color="primary" type="submit" onClick={this.logoutBtn} > Logout </Button></li>
          </ul>
        </div>

        {this.state.open === true ? <React.Fragment><Snackbar msg={this.state.message || 'Done'} open="true" /> </React.Fragment> : ' '}

      </React.Fragment>
    );
  }
}

export default withRouter(sidebar)