/* eslint-disable require-jsdoc */
import React, { Component } from 'react';
// import Breadcrumb from '../breadcrumb';
// import Modal from "react-responsive-modal";
import Button from '@material-ui/core/Button';
// import UploadDocuments from '../../container/uploadDocuments';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import { NavLink, Link } from 'react-router-dom';
import FileCopyIcon from '@material-ui/icons/FileCopy';

import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import PopupState, { bindTrigger, bindMenu } from 'material-ui-popup-state';

export default class header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      message: '',
      inputVal: '',
      anchorEl: false

    };

    this.handleInputValue = this.handleInputValue.bind(this);
  }

  onOpenModal = () => {
    this.setState({ open: true });
  };

  onCloseModal = () => {
    this.setState({ open: false });
  };

  handleInputValue(val) {
    this.setState({ inputVal: val });
  }

  handleClick = () => {
    this.setState({ anchorEl: true });
  };


  handleClose = () => {
    this.setState({ anchorEl: true });
  };


  render() {
    // const { open } = this.state;
    return (
      <div className="row">
        <div className="col-md-3 col-sm-12 align-self-center pageHeading">

          {this.props.backbtn === undefined ? ' ' :
            <Button id="backbtn" variant="outline" color="primary" href={"/hdfc/dashboard"}>
              <ArrowBackIosIcon></ArrowBackIosIcon>
            </Button>
          }
          {this.props.pageTitle} </div>
        <div className="col-md-9 col-sm-12 text-right align-self-center pageHeading">

          {this.props.download === undefined ? <NavLink to={"/upload-documents"}>
            <Button style={{ height: "35px" }} variant="contained" color="primary" onClick={this.onOpenModal}>Upload Statement</Button>
          </NavLink> :
            <React.Fragment>
              <a target="_blank" href={this.props.download}>
                <Button style={{ height: "35px" }} variant="contained" color="primary">
                  <FileCopyIcon></FileCopyIcon>
                      Download Excel
                      </Button>
              </a>

            </React.Fragment>
          }



          {/* <Button style={{ height: "35px" }} variant="contained" color="primary" onClick={this.onOpenModal}>Upload Statement</Button> */}
        </div>
        {/* <Modal open={open} onClose={this.onCloseModal}>
          <UploadDocuments handleInput={this.handleInputValue}   />
        </Modal> */}
      </div>
    );
  }
}
