/* eslint-disable require-jsdoc */
import React, { Component } from 'react';
import Header from '../../components/header';
import Sidebar from '../../components/sidebar';
import Button from '@material-ui/core/Button';
import Loader from '../../components/loader';
import Snackbar from '../../components/snackbar';
import axios from 'axios';
import MUIDataTable from 'mui-datatables';
import GetAppIcon from '@material-ui/icons/GetApp';
import VisibilityIcon from '@material-ui/icons/Visibility';

import DescriptionIcon from '@material-ui/icons/Description';
import noMatch from '../../images/noMatch.png';
import PopupState, { bindTrigger, bindMenu } from 'material-ui-popup-state';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import CloseIcon from '@material-ui/icons/Close';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Modal from 'react-bootstrap/Modal';
import { FilePond } from 'react-filepond';
import "filepond/dist/filepond.min.css";
import * as rax from 'retry-axios';
import { Form, Row, Col, Spinner } from 'react-bootstrap';
import { NavLink, Redirect, Link } from "react-router-dom";
import DeleteIcon from '@material-ui/icons/Delete';
import moment from 'moment';

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

export default class dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      token: sessionStorage.getItem('reactToken'),
      loading: false,
      getLoansLoader: false,
      open: false,
      message: '',
      showModal: false,
      showUploadModal: false,
      data: null,
      dashLength: null,
      dash_result: "",
      batchName: "",
      files: [],
      startdate: new Date(),
      enddate: new Date(),
    };
    // this.handleData = this.handleData.bind(this);
    this.openUpload = this.openUpload.bind(this);
    this.closeUpload = this.closeUpload.bind(this);
    this.getUsers = this.getUsers.bind(this);
    this.handleBatchNameChange = this.handleBatchNameChange.bind(this);
    this.exportData = this.exportData.bind(this);


    this.options = {
      filterType: 'select',
      search: false,
      print: false,
      viewColumns: false,
      download: false,
      rowHover: true,
      rowsPerPage: 15,
      rowsPerPageOptions: [15, 30, 60, 90],
      selectableRowsOnClick: true,
      selectableRows: false,
      onRowClick: this.onRowClick,
      onRowsSelect: this.onRowsSelect,
      textLabels: {
        body: {
          noMatch: <div className="noDataDisp justify-content-center">
            <img src={noMatch} />
            <h5>Upload new statements  to continue ...</h5>

            <Button style={{ height: '35px' }} onClick={this.openUpload} variant="contained" color="primary">Upload Statement</Button>

          </div>,
          toolTip: 'Sort',
          columnHeaderTooltip: (column) => `Sort for ${column.label}`,
        },
        filter: {
          all: 'All',
          title: 'FILTERS',
          reset: 'RESET',
        },

        selectedRows: {
          text: 'row(s) selected',
          delete: 'Delete',
          deleteAria: 'Delete Selected Rows',
        },

        customSort: (data, colIndex, order) => {
          console.log('data', data);
          return data.sort((a, b) => {
            return (a.data[colIndex].length < b.data[colIndex].length ? -1 : 1) * (order === 'desc' ? 1 : -1);
          });
        },
      },
    };
  }

  closeUpload() {
    this.setState({ showUploadModal: false });
  }

  openUpload() {
    this.setState({ showUploadModal: true });
    var today = new Date();
    var date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
    var time = today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
    var dateTime = date + ' ' + time;
    this.setState({ cur_date: dateTime, batchName: dateTime });
  }

  handleInit() {
    console.log("FilePond instance has initialised", this.pond);
  }

  handleBatchNameChange = (e) => {
    this.setState({ batchName: e.target.value });
  }

  getUsers = async (formData) => {
    try {
      var response = await axios(process.env.react_app_base_url + '/payslip/upload', {
        method: 'POST',
        data: formData,
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      // console.log(response);  


      if (response.data.message !== 'Successful!') {
        alert(response.data.message);
        return;
      }



      var bName = "";
      if (this.state.batchName == "") {
        bName = this.state.cur_date;
      } else {
        bName = this.state.batchName;
      }

      const data3 = {
        'token': this.state.token,
        'job_id': response.data.job_id,
        'document_name': response.data.document_name,
        'job_priority': "Medium",
        "batch_name": bName,
        "doc_type": this.state.doc_type
      }

      this.setState({ showUploadModal: false, dig_flag: false });
      const datatoken = { 'token': this.state.token };
      this.registerUser(datatoken);

      this.setState({ files: [] })

      var updArr = [];
      updArr.push(this.state.batchName);
      updArr.push("-");
      updArr.push("-");
      updArr.push(
        <div>
          <Spinner as="span" animation="border" variant="success" size="sm" role="status" aria-hidden="true" />{" In Process"}
        </div>
      );

      var newData = this.state.data;
      newData.unshift(updArr);

      this.setState({
        data: newData
      });

      await axios(process.env.react_app_base_url + '/payslip/digitize', {
        method: 'POST',
        data: JSON.stringify(data3),
        timeout: 600000,
        headers: {
          'Content-Type': 'application/json'
        },
        raxConfig: {
          retry: 0,

          noResponseRetries: 0,

          retryDelay: 100000000,

          httpMethodsToRetry: ['GET', 'HEAD', 'OPTIONS', 'DELETE', 'PUT'],

          backoffType: 'exponential',

          onRetryAttempt: err => {
            rax.getConfig(err);
            // console.log(`Retry attempt #${cfg.currentRetryAttempt}`);
          }
        }

      });

      // console.log(response3);
      // this.dashboard();
      const data = { 'token': this.state.token };
      this.registerUser(data);

    }
    catch (error) {
      if (error.response === undefined) {
        alert(error)
      }
      else {
        if (error.response.data.message === "Token is invalid!" || error.response.data.message === "Not Authorized!") {
          alert(error.response.data.message);
          this.setState({ tokenRed: true });
        }
        else {
          alert(error.response.data.message)
        }

      }
    }
  }

  digitize = () => {


    console.log("Asdasdas", this.pond.getFiles().length);
    // alert(this.state.doc_type)

    if (this.pond.getFiles().length === 0) {

      alert("Please upload the documents")
    } else {


      this.setState({ dig_flag: true });
      var formData = new FormData();

      var cnt = 0;

      this.pond.getFiles()
        .map(fileItem => fileItem.file)
        .forEach(file => {
          formData.append("file[" + cnt + "]", file, file.name);
          cnt++;
        });

      formData.append('token', this.state.token);

      // for (var key of formData.entries()) {
      // 	console.log(key[0] + ', ' + key[1]);
      // }
      // console.log(formData);

      this.getUsers(formData);

    }


  }

  async componentDidMount() {
    this.setState({ loading: true });

    const data = { 'token': this.state.token };
    this.registerUser(data);
  }
  async deleteJob(data) {
    console.log("data", data)

  }

  async registerUser(data) {
    console.log('dashboard', data);
    this.setState({ getLoansLoader: true });
    try {
      const res = await axios.post(process.env.react_app_base_url + '/payslip/user_dashboard', data);
      const resp = res.data;
      console.log('dashboard API', resp.result);
      console.log('Dashboard', resp);

      const sortedActivities = resp.result.sort((b, a) => new Date(b.start_date) - new Date(a.start_date));
      console.log('sortedActivities', sortedActivities);


      if (res.status === 200) {


        // {"admin_submitted":true,"admin_submitted_time":"2020-10-15 14:04","batch_name":"2020-10-15 08:29","batch_submitted_status":true,"document_name":"Job_7","excel_path":"static/data/excels/Job_7.xlsx","job_id":"7","job_priority":"Medium","job_size":"165.515 kB","job_status":"Validated by Admin","upload_date_time":"2020-10-15 13:59"},

        this.setState({ getLoansLoader: false, loading: false, open: true, message: resp.message });

        console.log("asdasdasdasd", res.data.result.length)
        this.setState({ dashLength: res.data.result.length })

        var dataArray = [];
        var timeSort = res.data.result;
        // console.log(timeSort);
        var sorted = timeSort.sort((a, b) => new Date(b.upload_date_time) - new Date(a.upload_date_time));
        console.log("sorted sorted sorted sorted sorted ", sorted);
        var resArr = sorted;

        for (var i in resArr) {

          var inArr = [];

          inArr.push(resArr[i].batch_name);
          inArr.push(resArr[i].upload_date_time);
          inArr.push(resArr[i].upload_date_time);
          inArr.push(resArr[i].job_status);
          inArr.push(resArr[i].job_status);
          inArr.push(resArr[i].job_status);
          inArr.push(resArr[i].excel_path);
          dataArray.push(inArr);
        }

        this.setState({
          dash_result: res.data.result,
        })
        this.setState({ data: res.data.result });
        // console.log(this.state.data);

      } else if (res.status === 201) {
        // alert('Failed 201');
        this.setState({ getLoansLoader: false, loading: false, open: true, message: resp.message });
        if (res.data.message == 'Token is invalid' || res.data.message == 'Not Authorized' || res.data.message == 'Token is missing!') {
          // toast.error(res.data.message);
          setTimeout(
            function () {
              this.props.history.push('/');
            }
              .bind(this),
            2000,
          );
        }
      } else {
        alert('Failed done');
      }
    } catch (error) {
      if (error.message === 'Network Error') {
        alert(JSON.stringify(error.message));
        this.setState({ open: true, message: error.message });
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );
      } else {
        console.log('error', JSON.stringify(error));
        alert('Something went wrong please try again');
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );
      }
    }
  }

  async downloadFIles(job_id) {
    console.log("data", job_id)

    const delete_data = {
      "token": sessionStorage.getItem('reactToken'),
      "job_id": job_id,
    }

    this.setState({ getLoansLoader: true });
    try {
      const res = await axios.post(process.env.react_app_base_url + '/payslip/download', delete_data,
        {
          responseType: 'blob',
          headers: {
            'Accept': 'application/vnd.ms-excel'
          }
        })
      const resp = res.data;
      console.log('dashboard API', resp.result);
      console.log('Dashboard', resp);
      if (res.status === 200) {
        const url = window.URL.createObjectURL(new Blob([resp]));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', "Job_id_" + job_id + ".xls"); 
        document.body.appendChild(link);
        link.click();
      } else if (res.status === 201) {
        if (res.data.message == 'Token is invalid' || res.data.message == 'Not Authorized' || res.data.message == 'Token is missing!') {
          setTimeout(
            function () {
              this.props.history.push('/');
            }
              .bind(this),
            2000,
          );
        }
      } else {
        alert('Failed done');
      }
    } catch (error) {
      if (error.message === 'Network Error') {
        alert(JSON.stringify(error.message));
        this.setState({ open: true, message: error.message });
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );
      } else {
        console.log('error', JSON.stringify(error));
        alert('Something went wrong please try again');
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );
      }
    }

  }


  handleChangeStartDate = date => {

    console.log("handleChangeStartDate", date)
    this.setState({
      startdate: date
    })
  }

  handleChangeEndDate = date => {

    console.log("handleChangeEndDate", date)
    this.setState({
      enddate: date
    })
  }

  async exportData() {
    

    const startdate = moment(this.state.startdate).format("YYYY-M-D");
    const enddate = moment(this.state.enddate).format("YYYY-M-D");

    console.log("this.state.startdate", startdate)
    console.log("this.state.enddate", enddate)

    const delete_data = {
      "token": sessionStorage.getItem('reactToken'),
      "day_start_date": startdate,
      "day_end_date": enddate
    }

    
    try {
      const res = await axios.post(process.env.react_app_base_url + '/payslip/excel_data', delete_data,
        {
          responseType: 'blob',
          headers: {
            'Accept': 'application/vnd.ms-excel'
          }
        })
      const resp = res.data;
      console.log('dashboard API', resp.result);
      console.log('Dashboard', resp);
      if (res.status === 200) {
        const url = window.URL.createObjectURL(new Blob([resp]));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', "Job_id_" + startdate+enddate+ ".xls"); 
        document.body.appendChild(link);
        link.click();
      } else if (res.status === 201) {
        if (res.data.message == 'Token is invalid' || res.data.message == 'Not Authorized' || res.data.message == 'Token is missing!') {
          setTimeout(
            function () {
              this.props.history.push('/');
            }
              .bind(this),
            2000,
          );
        }
      } else {
        alert('Failed done');
      }
    } catch (error) {
      if (error.message === 'Network Error') {
        alert(JSON.stringify(error.message));
        this.setState({ open: true, message: error.message });
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );
      } else {
        console.log('error', JSON.stringify(error));
        alert('Something went wrong please try again');
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );
      }
    }

  }

  render() {

    const columns = [
      {
        name: 'job_id',
        label: 'Sr No',
        options: {
          filter: false,
          sort: true,
          customBodyRender: (value, tableMeta) => (
            <div>
              {/* {JSON.stringify(tableMeta.rowData)} */}
              {/* {tableMeta.rowData[5] === 'Completed' ? <Button variant="outline" color="primary" href={'/job-details/' + value}>
              {tableMeta.rowIndex + 1}
                 <VisibilityIcon></VisibilityIcon>
              </Button> :
                <Button variant="outline" color="primary" disabled={true}>
                  {tableMeta.rowIndex + 1}
                  <VisibilityIcon></VisibilityIcon>
                </Button>
              } */}

              <Button variant="outline" color="primary">
                {tableMeta.rowIndex + 1}

              </Button>

            </div>
          ),
        },
      },

      {
        name: 'batch_name',
        label: 'Applicant Id',
        options: {
          filter: true,
          sort: true,
        },
      },
      // {
      //   name: 'bank_name',
      //   label: 'Bank Name',
      //   options: {
      //     filter: true,
      //     sort: true,
      //   },
      // },
      {
        name: 'upload_date_time',
        label: 'Processed On',
        options: {
          filter: true,
          sort: true,
        },
      },
      {
        name: 'job_status',
        label: 'Job Status',
        options: {
          filter: true,
          sort: true,
          customBodyRender: (value, tableMeta, updateValue) => (

            <div>
              {value}
              {/* {value === 'Complete' ? <span className="job_status completed">{value}</span> : ''}
              {value === 'Incomplete' ? <span className="job_status incomplete">{value}</span> : ''}
              {value === 'In Process' ? <span className="job_status processing">{value}</span> : ''}
              {value === 'Failed' ? <span className="job_status failed">{value}</span> : ''} */}
            </div>

          ),
        },
      },

      // {
      //   name: 'job_status',
      //   label: 'Download', options: {
      //     filter: false,
      //     sort: false,
      //     customBodyRender: (value, tableMeta) => (
      //       <div>
      //         {value === 'In Process' ? <span className="job_status incomplete">  <Button variant="outline" color="primary" disabled={true}> <CloseIcon /></Button></span> : <a target="_blank" href={process.env.react_app_base_url_image + tableMeta.rowData[6]}> <Button style={{ height: '35px' }} variant="outlined" color="primary"> <GetAppIcon /> </Button> </a>}
      //         {value === 'Processing' ? <span className="job_status processing">  <Button variant="outline" color="primary" disabled={true}> <CloseIcon /></Button></span> : ''}
      //         {value === 'Failed' ? <span className="job_status failed">  <Button variant="outline" color="primary" disabled={true}> <CloseIcon /></Button></span> : ''}
      //       </div>
      //     ),
      //   },
      // },
      {
        name: 'calculation_csv',
        label: 'Calculation CSV',
        options: {
          filter: false,
          sort: false,
          display: 'excluded',
        },
      },
      {
        name: 'excel_path',
        label: 'Excel Path',
        options: {
          filter: false,
          sort: false,
          display: 'excluded',
        },
      },

      {
        name: 'job_id',
        label: 'Download',
        options: {
          filter: true,
          sort: true,
          customBodyRender: (value, tableMeta, updateValue) => (

            <div>
              {/* {JSON.stringify(tableMeta.rowData[3])} */}
              {tableMeta.rowData[3] == "In Process" ? '' : <span className="deletespan">  <Button variant="text" color="primary" onClick={() => this.downloadFIles(value)} > <GetAppIcon /></Button></span>}

            </div>

          ),
        },
      },


    ];

    return (
      <React.Fragment>
        <div className="page">
          <Sidebar></Sidebar>
          <div className="content">
            <div className="header">

              <div className="row">
                <div className="col-md-3 col-sm-12 align-self-center pageHeading">

                  {this.props.backbtn === undefined ? ' ' :
                    <Button id="backbtn" variant="outline" color="primary" href="/dashboard">
                      <ArrowBackIosIcon></ArrowBackIosIcon>
                    </Button>
                  }
          Salary Slip Dashboard </div>
                <div className="col-md-9 col-sm-12 text-right align-self-center pageHeading">
                  <Button style={{ height: "35px" }} variant="contained" color="primary" onClick={this.openUpload} >Upload Salary Slip</Button>
                </div>

              </div>
            </div>
            <div className="page-content">

              {/* {JSON.stringify(this.state.data)} */}
              {/* {this.state.batchName} */}

              {this.state.loading === false ?
                <React.Fragment>
                  {
                    this.state.data ? <MUIDataTable
                      data={this.state.data}
                      columns={columns}
                      options={this.options}
                    /> : null
                  }
                </React.Fragment>
                : <Loader msg={'Please wait, Loading dashboard data'} />}

              {this.state.open === true ? <React.Fragment>
                {/* <Snackbar msg={this.state.message || 'Done'} open="true" /> */}
              </React.Fragment> : ' '}

              <div class="export-data">
                <div className="row">
                  <div className="col-md-3">
                    <label>Start Date</label>
                    <DatePicker
                      placeholderText="Start Date"
                      name="startdate"
                      dateFormat="d/MM/yyyy"
                      selected={this.state.startdate}
                      onChange={this.handleChangeStartDate}
                    />
                  </div>

                  <div className="col-md-3">
                    <label>End Date</label>
                    <DatePicker
                      placeholderText="Start Date"
                      name="startdate"
                      dateFormat="d/MM/yyyy"
                      selected={this.state.enddate}
                      onChange={this.handleChangeEndDate}
                    />
                  </div>
                  <div className="col-md-3">
                    <Button onClick={this.exportData} style={{ height: "35px", marginTop: "33px" }} variant="outlined" color="primary">
                      <FileCopyIcon></FileCopyIcon>
                      Export Excel
                      </Button>
                  </div>
                </div>
              </div>


            </div>
          </div>
        </div>



        <Modal size="lg" aria-labelledby="contained-modal-title-vcenter" centered className="uploadMod" show={this.state.showUploadModal} onHide={this.closeUpload}>

          <Modal.Body>
            <div className="row justify-content-center">
              <div className="col-md-12">
                <div id="borderBox1">

                  <FilePond
                    allowMultiple={true}
                    ref={ref => this.pond = ref}
                    oninit={() => this.handleInit()}
                    files={this.state.files}
                    onupdatefiles={fileItems => {

                      this.setState({
                        files: fileItems.map(fileItem => fileItem.file)
                      });
                    }}
                    name={"file"}
                    labelIdle='<b>Drag your documents here or <span class="filepond--label-action">Browse</span></b>'
                  />
                </div>
              </div>
            </div>
            <div id="batch">
              <Form>
                <div className="justify-content-center">
                  <Form.Group as={Row} controlId="formHorizontalEmail">
                    <Form.Label column sm={2}>Applicant Id</Form.Label>
                    <Col sm={4}>
                      <Form.Control style={{ width: '100%' }} className="textInput" placeholder="" onChange={this.handleBatchNameChange} value={this.state.batchName} />
                    </Col>
                  </Form.Group>




                </div>
              </Form>
            </div>

          </Modal.Body>

          <Modal.Footer>
            <Button className="modal_cancel_button" variant="secondary" onClick={this.closeUpload}>Cancel</Button>
            <Button className="modal_digitize_button" variant="primary" onClick={this.digitize} disabled={this.state.dig_flag}>
              {this.state.dig_flag ? <Spinner
                as="span"
                animation="border"
                size="sm"
                role="status"
                aria-hidden="true"
              /> : null}
							&nbsp; Process</Button>
          </Modal.Footer>
        </Modal>
      </React.Fragment>
    );
  }
}
