/* eslint-disable require-jsdoc */
import React, {Component} from 'react';
import Button from '@material-ui/core/Button';
import Loader from '../../../components/loader';
import Snackbar from '../../../components/snackbar';
import axios from 'axios';
import CircularProgress from '@material-ui/core/CircularProgress';
import {BrowserRouter as Router, Route, Link, NavLink} from 'react-router-dom';

import {withTheme} from 'react-jsonschema-form';
import {Theme as MuiTheme} from 'rjsf-material-ui';
import Logo from '../../../images/ind-logo.png';

// import Login from '../login';

const Form = withTheme(MuiTheme);

class activateUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      getLoansLoader: false,
      open: false,
      msg: '',
    };
  }


  async componentDidMount() {
    console.log('this.props.location', this.props.location.search);
    this.setState({loading: true});
    const data = this.props.location.search;
    this.registerUser(data);
  };


  async registerUser(data) {
    this.setState({getLoansLoader: true, loading: true});
    //  http://localhost:3000/ActivateCustomer?emailid=balaji.image@gmail.com&custid=asdasd11&key=f8db0ade-c287-4cd4-8f56-fe0aaa9e575b
    try {
      const res = await axios.get(process.env.react_app_base_url+'/credit/activate_admin' + data);
      const resp = res.data;
      console.log('response', resp);
      if (res.status === 200) {
        // console.log("eligibleloans", resp);
        this.setState({getLoansLoader: false, open: true, loading: false, msg: res.data.message});
      } else if (res.status === 201) {
        // alert("HI")
        // console.log("eligibleloans", resp);
        this.setState({getLoansLoader: false, open: true, loading: false, msg: res.data.message});
      } else {
        alert('Failed');
      }
    } catch (error) {
      if (error.message === 'Network Error') {
        alert(JSON.stringify(error.message));
        this.setState({open: true, message: error.message});
        setTimeout(
            function() {
              this.props.history.push('/');
            }
                .bind(this),
            2000,
        );
      } else {
        console.log('error', JSON.stringify(error));
        alert('Something went wrong please try again');
        setTimeout(
            function() {
              this.props.history.push('/');
            }
                .bind(this),
            2000,
        );
      }
    }
  };


  render() {
    return (
      <React.Fragment>
        <div className="page" id="site-login">

          <div className="logo">
            <img alt={'IN-D Credit'} src={Logo} ></img>
          </div>

          <div className="login-body">

            {this.state.loading === false ?
              <React.Fragment>
                {/* <Login></Login> */}
                <div className="activateUser">
                  <h5>IN-D Credit Admin account activation confirmation</h5>
                  <p>Your Admin account has been actvated. You can now access the application with your credentials using the following link</p>
                  <a href="/admin/index.html"> <Button variant="contained" color="primary" >Click here to login</Button></a>
                  <p>You are receiving this email because you activated an IN-D Credit Admin account. If you believe you have received this email in error, please mail us to
                    <a href="mailto:explore@in-d.ai" title="explore@in-d.ai"> explore@in-d.ai</a></p>
                </div>
              </React.Fragment> :

               <Loader msg={'Please wait...'} />}

            {this.state.open === true ? <React.Fragment><Snackbar msg={this.state.msg} open="true" /> </React.Fragment> : ' '}


          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default activateUser;
