/* eslint-disable require-jsdoc */
import React, { Component } from 'react';
import Header from '../../components/header';
import Sidebar from '../../components/sidebar';
import Button from '@material-ui/core/Button';
import Loader from '../../components/loader';
import Snackbar from '../../components/snackbar';
import axios from 'axios';
import MUIDataTable from 'mui-datatables';
import GetAppIcon from '@material-ui/icons/GetApp';
import VisibilityIcon from '@material-ui/icons/Visibility';
import { NavLink } from 'react-router-dom';
import DescriptionIcon from '@material-ui/icons/Description';
import noMatch from '../../images/noMatch.png';
import PopupState, { bindTrigger, bindMenu } from 'material-ui-popup-state';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import CloseIcon from '@material-ui/icons/Close';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import DeleteIcon from '@material-ui/icons/Delete';
import moment from 'moment';

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

export default class dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      token: sessionStorage.getItem('reactToken'),
      loading: false,
      getLoansLoader: false,
      open: false,
      message: '',
      startdate: new Date(),
      enddate: new Date(),
    };
    // this.handleData = this.handleData.bind(this);
    this.exportData = this.exportData.bind(this);



    this.options = {
      filterType: 'select',
      search: false,
      print: false,
      viewColumns: false,
      download: false,
      rowHover: true,
      rowsPerPage: 15,
      rowsPerPageOptions: [15, 30, 60, 90],
      selectableRowsOnClick: true,
      selectableRows: false,
      onRowClick: this.onRowClick,
      onRowsSelect: this.onRowsSelect,
      textLabels: {
        body: {
          noMatch: <div className="noDataDisp justify-content-center">
            <img src={noMatch} />
            <h5>Upload new statements  to continue ...</h5>
            <NavLink to="/upload-documents">
              <Button style={{ height: '35px' }} variant="contained" color="primary">Upload Statement</Button>
            </NavLink>
          </div>,
          toolTip: 'Sort',
          columnHeaderTooltip: (column) => `Sort for ${column.label}`,
        },
        filter: {
          all: 'All',
          title: 'FILTERS',
          reset: 'RESET',
        },

        selectedRows: {
          text: 'row(s) selected',
          delete: 'Delete',
          deleteAria: 'Delete Selected Rows',
        },

        customSort: (data, colIndex, order) => {
          console.log('data', data);
          return data.sort((a, b) => {
            return (a.data[colIndex].length < b.data[colIndex].length ? -1 : 1) * (order === 'desc' ? 1 : -1);
          });
        },
      },
    };
  }
  handleDateSelect(e) {
    console.log(e)
  }

  async componentDidMount() {
    this.setState({ loading: true });

    const data = { 'token': this.state.token };
    this.registerUser(data);
  }

  async deleteJob(data) {
    console.log("data", data)

    const delete_data = {
      "token": sessionStorage.getItem('reactToken'),
      "job_id": data,
    }

    this.setState({ getLoansLoader: true });
    try {
      const res = await axios.post(process.env.react_app_base_url + '/credit/delete_job_data', delete_data);
      const resp = res.data;
      console.log('dashboard API', resp.result);
      console.log('Dashboard', resp);

      if (res.status === 200) {
        alert("deleted")
        const deleteToken = { 'token': this.state.token };
        this.registerUser(deleteToken);
      } else if (res.status === 201) {
        if (res.data.message == 'Token is invalid' || res.data.message == 'Not Authorized' || res.data.message == 'Token is missing!') {
          setTimeout(
            function () {
              this.props.history.push('/');
            }
              .bind(this),
            2000,
          );
        }
      } else {
        alert('Failed done');
      }
    } catch (error) {
      if (error.message === 'Network Error') {
        alert(JSON.stringify(error.message));
        this.setState({ open: true, message: error.message });
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );
      } else {
        console.log('error', JSON.stringify(error));
        alert('Something went wrong please try again');
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );
      }
    }

  }



  async exportData() {

    // console.log("this.state.startdate", this.state.startdate)
    // console.log("this.state.enddate", this.state.enddate)

    const startdate = moment(this.state.startdate).format("YYYY-M-D");
    const enddate = moment(this.state.enddate).format("YYYY-M-D");

    console.log("this.state.startdate", startdate)
    console.log("this.state.enddate", enddate)

    const delete_data = {
      "token": sessionStorage.getItem('reactToken'),
      "day_start_date": startdate,
      "day_end_date": enddate
    }

    this.setState({ getLoansLoader: true });
    try {
      const res = await axios.post(process.env.react_app_base_url + '/credit/excel_data', delete_data);
      const resp = res.data;
      console.log('dashboard API', resp.result);
      console.log('Dashboard', resp);

      if (res.status === 200) {
        // alert("exportData")
        
        
            // const url = window.URL.createObjectURL(new Blob([process.env.react_app_base_url+"/"+resp.excel_path]));
            const link = document.createElement('a');
            link.href = process.env.react_app_base_url+"/"+resp.excel_path;
            link.setAttribute('download', "export_data"+resp.excel_path); //or any other extension
            document.body.appendChild(link);
            link.click();

      } else if (res.status === 201) {
        if (res.data.message == 'Token is invalid' || res.data.message == 'Not Authorized' || res.data.message == 'Token is missing!') {
          setTimeout(
            function () {
              this.props.history.push('/');
            }
              .bind(this),
            2000,
          );
        }
      } else {
        alert('Failed done');
      }
    } catch (error) {
      if (error.message === 'Network Error') {
        alert(JSON.stringify(error.message));
        this.setState({ open: true, message: error.message });
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );
      } else {
        console.log('error', JSON.stringify(error));
        alert('Something went wrong please try again');
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );
      }
    }

  }


  async registerUser(data) {
    console.log('dashboard', data);

    this.setState({ getLoansLoader: true });
    try {
      const res = await axios.post(process.env.react_app_base_url + '/credit/user_dashboard', data);
      const resp = res.data;
      console.log('dashboard API', resp.result);
      console.log('Dashboard', resp);

      const sortedActivities = resp.result.sort((b, a) => new Date(b.start_date) - new Date(a.start_date));
      console.log('sortedActivities', sortedActivities);


      if (res.status === 200) {
        this.setState({ getLoansLoader: false, loading: false, open: true, tableData: sortedActivities.reverse(), message: resp.message });
      } else if (res.status === 201) {
        // alert('Failed 201');
        this.setState({ getLoansLoader: false, loading: false, open: true, message: resp.message });
        if (res.data.message == 'Token is invalid' || res.data.message == 'Not Authorized' || res.data.message == 'Token is missing!') {
          // toast.error(res.data.message);
          setTimeout(
            function () {
              this.props.history.push('/');
            }
              .bind(this),
            2000,
          );
        }
      } else {
        alert('Failed done');
      }
    } catch (error) {
      if (error.message === 'Network Error') {
        alert(JSON.stringify(error.message));
        this.setState({ open: true, message: error.message });
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );
      } else {
        console.log('error', JSON.stringify(error));
        alert('Something went wrong please try again');
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );
      }
    }

  }

  handleChangeStartDate = date => {

    console.log("handleChangeStartDate", date)
    this.setState({
      startdate: date
    })
  }

  handleChangeEndDate = date => {

    console.log("handleChangeEndDate", date)
    this.setState({
      enddate: date
    })
  }

  render() {

    const columns = [
      {
        name: 'job_id',
        label: 'Sr No',
        options: {
          filter: false,
          sort: true,
          customBodyRender: (value, tableMeta) => (
            <div>
              {/* {JSON.stringify(tableMeta.rowData)} */}
              {/* {tableMeta.rowData[5] === 'Completed' ? <Button variant="outline" color="primary" href={'/job-details/' + value}>
              {tableMeta.rowIndex + 1}
                 <VisibilityIcon></VisibilityIcon>
              </Button> :
                <Button variant="outline" color="primary" disabled={true}>
                  {tableMeta.rowIndex + 1}
                  <VisibilityIcon></VisibilityIcon>
                </Button>
              } */}

              <Button variant="outline" color="primary">
                {tableMeta.rowIndex + 1}

              </Button>

            </div>
          ),
        },
      },
      // {
      //   name: 'job_id',
      //   label: 'Job ID',
      //   options: {
      //     filter: false,
      //     sort: true,
      //     customBodyRender: (value, tableMeta) => (
      //       <div>
      //         {value}

      //       </div>
      //     ),
      //   },
      // },
      {
        name: 'applicant_id',
        label: 'Applicant Id',
        options: {
          filter: true,
          sort: true,
        },
      },
      {
        name: 'bank_name',
        label: 'Bank Name',
        options: {
          filter: true,
          sort: true,
        },
      },
      {
        name: 'upload_date_time',
        label: 'Processed On',
        options: {
          filter: true,
          sort: true,
        },
      },
      {
        name: 'comment',
        label: 'Comment',
        options: {
          filter: true,
          sort: true,
        },
      },
      {
        name: 'job_status',
        label: 'Job Status',
        options: {
          filter: true,
          sort: true,
          customBodyRender: (value, tableMeta, updateValue) => (

            <div>
              {value === 'Completed' ? <span className="job_status completed">{value}</span> : ''}
              {value === 'Incomplete' ? <span className="job_status incomplete">{value}</span> : ''}
              {value === 'Processing' ? <span className="job_status processing">{value}</span> : ''}
              {value === 'Failed' ? <span className="job_status failed">{value}</span> : ''}

            </div>

          ),
        },
      },

      {
        name: 'job_status',
        label: 'Download', options: {
          filter: false,
          sort: false,
          customBodyRender: (value, tableMeta) => (
            <div>

              {/* {JSON.stringify(tableMeta.rowData)} */}
              {value === 'Completed' ?
                <div className="downloadspan" style={{ position: 'relative' }}>
                  <React.Fragment>
                    <a target="_blank" href={process.env.react_app_base_url_image + tableMeta.rowData[8]}> <Button style={{ height: '35px' }} variant="outlined" color="primary"> <GetAppIcon /> </Button> </a>
                    {/* <PopupState variant="popover" popupId="demo-popup-menu12">
                    {(popupState) => (
                      <React.Fragment>
                        <Button style={{height: '35px'}} variant="outline" color="primary" {...bindTrigger(popupState)}>
                          <GetAppIcon />
                        </Button>
                        <Menu {...bindMenu(popupState)}>

                          <li className="menuItem"><a target="_blank" href={process.env.react_app_base_url_image + tableMeta.rowData[6]}> <Button style={{height: '35px'}} variant="contained" color="primary" onClick={popupState.close}><FileCopyIcon></FileCopyIcon>  Transactions CSV</Button> </a></li>
                          <li className="menuItem"><a target="_blank" href={process.env.react_app_base_url_image + tableMeta.rowData[7]}> <Button style={{height: '35px'}} variant="contained" color="primary" onClick={popupState.close}><FileCopyIcon></FileCopyIcon>  Calculations CSV</Button> </a></li>
                        </Menu>
                      </React.Fragment>
                    )}
                  </PopupState> */}

                  </React.Fragment> </div> : ''}
              {value === 'Incomplete' ? <span className="job_status incomplete">  <Button variant="outline" color="primary" disabled={true}> <CloseIcon /></Button></span> : ''}
              {value === 'Processing' ? <span className="job_status processing">  <Button variant="outline" color="primary" disabled={true}> <CloseIcon /></Button></span> : ''}
              {value === 'Failed' ? <span className="job_status failed">  <Button variant="outline" color="primary" disabled={true}> <CloseIcon /></Button></span> : ''}

            </div>
          ),
        },
      },

      // deleteJob
      {
        name: 'calculation_csv',
        label: 'Calculation CSV',
        options: {
          filter: false,
          sort: false,
          display: 'excluded',
        },
      },
      {
        name: 'excel_path',
        label: 'Excel Path',
        options: {
          filter: false,
          sort: false,
          display: 'excluded',
        },
      },
      // {
      //   name: 'job_id',
      //   label: 'Delete',
      //   options: {
      //     filter: true,
      //     sort: true,
      //     customBodyRender: (value, tableMeta, updateValue) => (

      //       <div>
      //         <span className="deletespan">  <Button variant="text" color="primary" onClick={() => this.deleteJob(value)} > <DeleteIcon /></Button></span>
      //       </div>

      //     ),
      //   },
      // },
    ];

    return (
      <React.Fragment>
        <div className="page">

          <Sidebar></Sidebar>
          <div className="content">
            <div className="header"><Header pageTitle={'Bank Statement Dashboard'}></Header></div>

            <div className="page-content">

              {this.state.loading === false ?
                <MUIDataTable
                  data={this.state.tableData}
                  columns={columns}
                  options={this.options}

                /> : <Loader msg={'Please wait, Loading dashboard data'} />}

              {this.state.open === true ? <React.Fragment>
                {/* <Snackbar msg={this.state.message || 'Done'} open="true" /> */}
              </React.Fragment> : ' '}
              <div class="export-data">
                <div className="row">
                  <div className="col-md-3">
                    <label>Start Date</label>
                    <DatePicker
                      placeholderText="Start Date"
                      name="startdate"
                      dateFormat="d/MM/yyyy"
                      selected={this.state.startdate}
                      onChange={this.handleChangeStartDate}
                    />
                  </div>

                  <div className="col-md-3">
                    <label>End Date</label>
                    <DatePicker
                      placeholderText="Start Date"
                      name="startdate"
                      dateFormat="d/MM/yyyy"
                      selected={this.state.enddate}
                      onChange={this.handleChangeEndDate}
                    />
                  </div>
                  <div className="col-md-3">
                    <Button onClick={this.exportData} style={{ height: "35px", marginTop: "33px" }} variant="outlined" color="primary">
                      <FileCopyIcon></FileCopyIcon>
                      Export Excel
                      </Button>
                  </div>
                </div>
              </div>

            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
