/* eslint-disable require-jsdoc */
import React, { Component } from 'react';
import Header from '../../components/header';
import Sidebar from '../../components/sidebar';
import Button from '@material-ui/core/Button';
import Loader from '../../components/loader';
import Snackbar from '../../components/snackbar';
import axios from 'axios';
import CircularProgress from '@material-ui/core/CircularProgress';
import MUIDataTable from 'mui-datatables';
import GetAppIcon from '@material-ui/icons/GetApp';
import LinkItem from '../../components/linkItem';
import DonutSmallIcon from '@material-ui/icons/DonutSmall';
import TextField from '@material-ui/core/TextField';
import SearchResults from 'react-filter-search';
import { Doughnut } from 'react-chartjs-2';
import { Bar } from 'react-chartjs-2';
import { Line } from 'react-chartjs-2';

import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import ViewAgendaIcon from '@material-ui/icons/ViewAgenda';
import PieChartIcon from '@material-ui/icons/PieChart';

function TabContainer(props) {
  return (
    <Typography component="div" style={{ padding: 8 * 3 }}>
      {props.children}
    </Typography>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
};


export default class jobdetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      token: sessionStorage.getItem('reactToken'),
      loading: false,
      getLoansLoader: false,
      open: false,
      message: '',
      jobID: '',
      calculation_response: [],
      calculation_result_list: null,
      calculation_result_list_new: null,
      calculation_csv: '',
      value: '',
      data: [],
      Credit: [],
      monthlyAverage: [],
      Debit: [],
      Transactions: [],
      search: "",
      value: 0,
      transaction_data: null,
      excel_path: null,
      searchType: false

    };
  }

  async componentDidMount() {
    this.setState({ loading: true });


    const jobID = this.props.match.params.id;
    this.setState({ jobID: jobID });

    const data = { 'token': this.state.token, 'job_id': jobID };

    this.registerUser(data);

  };

  async registerUser(data) {
    console.log('dashboard', data);
    this.setState({ getLoansLoader: true });
    try {
      const res = await axios.post(process.env.react_app_base_url + '/credit/getdetailsbyid', data);
      const resp = res.data;



      if (res.status === 201) {

        this.setState({ getLoansLoader: false, loading: false, open: true, message: resp.message });

        if (res.data.message == 'Token is invalid' || res.data.message == 'Not Authorized' || res.data.message == 'Token is missing!') {


          setTimeout(
            function () {
              this.props.history.push('/');
            }
              .bind(this),
            2000,
          );

        }
      } else if (res.status === 200) {

        // alert(typeof resp.result.calculation_csv_path_new)
        // alert(typeof resp.result.excel_path)
        // alert(typeof resp.result.calculation_response)
        // alert(typeof resp.result.calculation_result_list)
        // alert(typeof resp.result.transaction_data)


        this.setState({ getLoansLoader: false, loading: false, open: true, message: resp.message });

        if (resp.result.calculation_csv_path_new !== undefined) {

          const calculation_csv_path_new = process.env.react_app_base_url_image + resp.result.calculation_csv_path_new;
          console.log("calculation_csv_path_new", calculation_csv_path_new)
          this.setState({ calculation_csv: calculation_csv_path_new })

        }

        if (resp.result.excel_path !== undefined) {

          const excel_path = process.env.react_app_base_url_image + resp.result.excel_path;
          this.setState({ excel_path: excel_path })

        }

        if (resp.result.calculation_result_list !== undefined) {

        const calculation_result_list = resp.result.calculation_result_list;
        this.setState({ calculation_result_list: calculation_result_list })

        }

        if (resp.result.calculation_response !== undefined) {

          console.log('newobj calculation_response', typeof (resp.result.calculation_response));
          
          const ndddd = resp.result.calculation_response;
          console.log('ndddd', ndddd);
          const b = ndddd.replace(/'/g, '"');
          console.log(b);
          const obj = JSON.parse(b);
          console.log('obj', obj);
          this.setState({ calculation_response: obj });

          this.setState({
            AccountHolder: obj['01. Account Holder'],
            AccountNumber: obj['02. Account Number'],
            StatementStartDate: obj['03. Statement Start Date'],
            StatementEndDate: obj['04. Statement End Date'],
            OpeningBalance: obj['09. Opening Balance'],
            TotalCreditedAmount: obj['13. Total amount credited'],
            TotalDebitedAmount: obj['14. Total amount debited'],
            ClosingBalance: obj['10. Closing Balance']
          });

          let credit_sub_total = obj['32. Total amount credited through salary'] + obj['40. Total amount credited through cheque'] + obj['20. Total amount credited through UPI'] + obj['24. Total amount credited through net banking'] + obj['16. Total amount of cash deposited'];
          // alert(debit_sub_total)
          let total_amount_credit_chart = obj['13. Total amount credited'] - credit_sub_total;

          const Credit = {
            labels: [
              'Cheque',
              'UPI',
              'Net Banking',
              'Cash Deposit',
              'Salary',
              'Other',
            ],
            datasets: [
              {
                data: [
                  obj['40. Total amount credited through cheque'],
                  obj['20. Total amount credited through UPI'],
                  obj['24. Total amount credited through net banking'],
                  obj['16. Total amount of cash deposited'],
                  obj['32. Total amount credited through salary'],
                  total_amount_credit_chart,
                ],
                backgroundColor: [
                  '#FF6384',
                  '#f3d2ba',
                  '#84FF63',
                  '#392be891',
                  '#67d6c3',
                ],
              }],
          };

          let debit_sub_total = obj['38. Total amount debited through cheque'] + obj['22. Total amount debited through UPI'] + obj['26. Total amount debited through net banking'] + obj['18. Total amount of cash withdrawed'] + obj['66. Total amount spent at merchant outlets'];
          // alert(debit_sub_total)
          let total_amount_chart = obj['14. Total amount debited'] - debit_sub_total;

          var Debit = {
            labels: [
              'Cheque',
              'UPI',
              'Net Banking',
              'Cash Withdraw',
              'Merchant Outlets',
              'Other',
            ],
            datasets: [
              {
                data: [
                  obj['38. Total amount debited through cheque'],
                  obj['22. Total amount debited through UPI'],
                  obj['26. Total amount debited through net banking'],
                  obj['18. Total amount of cash withdrawed'],
                  obj['66. Total amount spent at merchant outlets'],
                  total_amount_chart,
                ],
                backgroundColor: [
                  '#FF6384',
                  '#f3d2ba',
                  '#84FF63',
                  '#392be891',
                  '#67d6c3',
                ],
              }],
          };

          const monthlyAverage = {
            labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'June', 'July', 'Aug', 'Sept', 'Oct', 'Nov', 'Dec'],
            datasets: [{
              label: 'Monthly Average Balance', // Name the series
              data: [5056, 88942, 64545, 50001, 45555, 36516, 22224, 5504, 14629, 4545, 1417, 15504], // Specify the data values array
              fill: false,
              borderColor: '#4CAF50', // Add custom color border (Line)
              backgroundColor: '#4CAF50', // Add custom color background (Points and Fill)
              borderWidth: 1, // Specify bar border width
            }],
          };


          let number_debit_other_sub = obj['21. Number of UPI debit transactions'] + obj['25. Number of net banking debit transactions'] + obj['37. Number of debit transactions through cheque'] + obj['17. Number of cash withdrawal transactions'];
          let number_debit_other = obj['12. Number of debit transactions'] - number_debit_other_sub;
          console.log('number_debit_other', number_debit_other);
          console.log('obj[\'12. Number of debit transactions\']  -  number_debit_other_sub', obj['12. Number of debit transactions'] - number_debit_other_sub);
          // var number_credit_other = ;

          let number_credit_other_sub = obj['19. Number of UPI credit transactions'] + obj['23. Number of net banking credit transactions'] + obj['39. Number of credit transactions through cheque'] + obj['15. Number of cash deposit transactions'];
          let number_credit_other = obj['11. Number of credit transactions'] - number_credit_other_sub;

          console.log('obj[\'11. Number of credit transactions\']  -  number_credit_other_sub', obj['12. Number of debit transactions'] - number_credit_other_sub);
          let densityData = {
            label: 'Debit : ' + obj['12. Number of debit transactions'] + '',
            data: [
              obj['21. Number of UPI debit transactions'],
              obj['25. Number of net banking debit transactions'],
              obj['37. Number of debit transactions through cheque'],
              obj['17. Number of cash withdrawal transactions'],
              number_debit_other,


              // obj['Total amount debited']
            ],
            backgroundColor: 'rgba(0, 99, 132, 0.6)',
            borderWidth: 0,
            yAxisID: 'y-axis-density',
          };

          let gravityData = {
            label: 'Credit : ' + obj['11. Number of credit transactions'] + '',
            data: [
              obj['19. Number of UPI credit transactions'],
              obj['23. Number of net banking credit transactions'],
              obj['39. Number of credit transactions through cheque'],
              obj['15. Number of cash deposit transactions'],
              number_credit_other,
            ],
            backgroundColor: 'rgba(99, 132, 0, 0.6)',
            borderWidth: 0,
            yAxisID: '',
          };

          const Transactions = {
            labels: [
              'Cheque',
              'UPI',
              'Net Banking',
              'Cash Withdraw',
              'Merchant Outlets',
              'Other',
            ],
            datasets: [
              {
                label: 'Debit : ' + obj['12. Number of debit transactions'] + '',
                backgroundColor: 'rgba(0, 99, 132, 0.6)',
                borderColor: 'rgba(0, 99, 132, 0.6)',
                borderWidth: 1,
                data: [
                  obj['21. Number of UPI debit transactions'],
                  obj['25. Number of net banking debit transactions'],
                  obj['37. Number of debit transactions through cheque'],
                  obj['17. Number of cash withdrawal transactions'],
                  number_debit_other,


                  // obj['Total amount debited']
                ],
              },
              {
                label: 'Credit : ' + obj['11. Number of credit transactions'] + '',
                backgroundColor: 'rgba(99, 132, 0, 0.6)',
                borderColor: 'rgba(99, 132, 0, 0.6)',
                borderWidth: 1,
                data: [
                  obj['19. Number of UPI credit transactions'],
                  obj['23. Number of net banking credit transactions'],
                  obj['39. Number of credit transactions through cheque'],
                  obj['15. Number of cash deposit transactions'],
                  number_credit_other,
                ],
              }
            ]
          };

          this.setState({ Credit: Credit, Debit: Debit, monthlyAverage: monthlyAverage, Transactions: Transactions })

        }

        if (resp.result.transaction_data !== undefined) {

          const transaction_data = resp.result.transaction_data
          console.log("transaction_data", transaction_data)

          transaction_data.sort(function (a, b) {
            var dateA = new Date(a.Date);
            var dateB = new Date(b.Date);
            return dateA - dateB;
          });

          console.log("transaction_data after", transaction_data)

          console.log("resp", resp.result)

          this.setState({ transaction_data: transaction_data })

        }



      }

    } catch (error) {
      if (error.message === 'Network Error') {
        alert(JSON.stringify(error.message));
        this.setState({ open: true, message: error.message });
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );
      } else {
        console.log("error", JSON.stringify(error))
        alert('Something went wrong please try again');
        setTimeout(
          function () {
            this.props.history.push('/');
          }
            .bind(this),
          2000,
        );
      }
    }
  };

  onchange = e => {

    this.setState({ search: e.target.value, searchType: true });
    console.log("search: e.target.value", e.target.value);
    const poetFilter = e.target.value
    let filteredPoets = e.target.value;
    // const calculation_result_list_const = this.state.calculation_result_list
    // console.log("filteredPoets", calculation_result_list_const)
    filteredPoets = this.state.calculation_result_list.filter((poet) => {
      let poetName = poet.name.toLowerCase();
      return poetName.indexOf(
        poetFilter.toLowerCase()) !== -1
    })
    this.setState({ calculation_result_list_new: filteredPoets })
    console.log("filteredPoets", filteredPoets)

  };

  handleChange = (event, value) => {
    this.setState({ value });
  };


  render() {

    console.log("calculation_responseasdasdasd sadas", this.state.calculation_response);
    const { value } = this.state;


    return (
      <React.Fragment>
        <div className="page">
          <Sidebar></Sidebar>
          <div className="content">

            <div className="header"><Header pageTitle={this.state.jobID} backbtn={true} download={this.state.calculation_csv} excel_path={this.state.excel_path} ></Header></div>

            <div className="header-sub">
              <div className="row">
                <div className="text-right col-md-7">

                  {/* <ul className="cards-charts">
                    <li className="logoPlaceholder"><LinkItem to={'#nogo'} title={'Charts'} icon={'LeftMenuIcon1.png'}> </LinkItem></li>
                    <li className="logoPlaceholder"><LinkItem to={'#nogo'} title={'Cards'} icon={'LeftMenuIcon4.png'}> </LinkItem></li>
                  </ul> */}

                  <Tabs value={value} onChange={this.handleChange} className="cards-charts">
                    <Tab label="Charts" icon={<PieChartIcon />} />
                    <Tab label="Cards" icon={<ViewAgendaIcon />} />
                    {/* <Tab label="Transaction" icon={<ViewAgendaIcon />} /> */}
                  </Tabs>

                </div>
                <div className="text-right col-md-5"> <TextField id="outlined-basic" onChange={this.onchange} label="Search" variant="outlined" />
                </div>
              </div>
            </div>




            {value === 0 && <TabContainer>
              {this.state.loading === false ?
                <div className="container-fluid" id="charts">

                  <div className="row">
                    <div className="col-md-6">
                      <div className="innerBox">
                        <h5>Account Information</h5>
                        <table>
                          <tbody>
                            <tr>
                              <td>Account Holder</td>
                              <td className="account_holder">{this.state.AccountHolder}</td>
                            </tr>
                            <tr>
                              <td>Account Number</td>
                              <td className="account_number">{this.state.AccountNumber}</td>
                            </tr>

                            <tr>
                              <td>Statement Start Date</td>
                              <td className="Statement_start_date">{this.state.StatementStartDate}</td>
                            </tr>

                            <tr>
                              <td>Statement End Date</td>
                              <td className="Statement_end_date">{this.state.StatementEndDate}</td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                    <div className="col-md-6">
                      <div className="innerBox">
                        <h5>Account Summary</h5>
                        <table>
                          <tbody>
                            <tr>
                              <td>Opening Balance</td>
                              <td className="opening_balance">{this.state.OpeningBalance}</td>
                            </tr>
                            <tr>
                              <td>Total Debited Amount
                                              </td>
                              <td className="total_amount_debited">{this.state.TotalDebitedAmount}</td>
                            </tr>
                            <tr>
                              <td>Total Credited Amount
                                              </td>
                              <td className="total_amount_credit">{this.state.TotalCreditedAmount}</td>
                            </tr>

                            <tr>
                              <td>Closing Balance</td>
                              <td className="closing_balance">{this.state.ClosingBalance}</td>
                            </tr>
                          </tbody>

                        </table>
                      </div>
                    </div>
                    <div className="clearfix"></div>
                  </div>

                  <div className="row text-center">
                    <div className="col-md-6">
                      <div className="innerBox">
                        <h5>Credit</h5>
                        <Doughnut data={this.state.Credit} />
                        <p className="param">Total Credit : <span id="total_credited">{this.state.TotalCreditedAmount}</span></p>
                      </div>
                    </div>
                    <div className=" col-md-6">
                      <div className="innerBox">
                        <h5>Debit</h5>
                        <Doughnut data={this.state.Debit} />
                        <p className="param">Total Debit : <span id="total_credited">{this.state.TotalDebitedAmount}</span></p>
                      </div>
                    </div>
                  </div>
                  <div className="row text-center">
                    {/* <div className="col-md-6">
                      <div className="innerBox" style={{ height: '500px' }}>
                        <h5>Monthly Average Balance</h5>
                        <Line data={this.state.monthlyAverage} />
                      </div>
                    </div> */}
                    <div className="col-md-6">
                      <div className="innerBox" style={{ height: '500px' }}>
                        <h5>Number of Transactions</h5>



                        <Bar
                          data={this.state.Transactions}
                          width={100}
                          height={50}
                          options={{
                            maintainAspectRatio: false
                          }}
                        />
                        <div className="divider"></div>
                      </div>
                    </div>
                  </div>
                </div>
                :

                <Loader msg={'Please wait, Loading Job Details'} />}

            </TabContainer>}


            {value === 1 && <TabContainer>

              <div className="page-content nobg">
                {this.state.loading === false ?
                  <div className="row">




                    {this.state.searchType === true ?
                      this.state.calculation_result_list_new.map((item) => {
                        return (
                          <div className="col-sm-4" key={item.id}>
                            <div className="innerbox">
                              <div className="key-name">
                                {item.name}
                              </div>
                              <div className="value">
                                {item.value}
                              </div>
                            </div>
                          </div>

                        );
                      })

                      : this.state.calculation_result_list === undefined ? '' :

                        this.state.calculation_result_list !== null ?

                          this.state.calculation_result_list.map((item) => {

                            return (

                              <div className="col-sm-4" key={item.id}>
                                <div className="innerbox">
                                  <div className="key-name">
                                    {item.name}
                                  </div>
                                  <div className="value">
                                    {item.value}
                                  </div>
                                </div>
                              </div>

                            );
                          })
                          : 'No data available in table'}




                  </div> :

                  <Loader msg={'Please wait, Loading Job Details'} />}




              </div>

            </TabContainer>}

            {/* {value === 2 && <TabContainer>
<div className="row" id="transactionData">
          <div className="col-md-12">
                        <div className="innerBox">
                          
                        <table>
        <tbody>

       

{this.state.transaction_data
  // .sort((a, b) => new Date(a.Date) + new Date(b.Date))
  .map((item) => {
                    return (
                      
        <tr  key={item.Date}>
          <td> {item.Date}</td>
          <td>{item.Description}</td>
          <td>{item.Credit}</td>
          <td>{item.Debit}</td>
          <td> {item.Balance}</td>
        </tr>
       
                    );
                  })
                  }


        </tbody>
      </table>

                           
                            
      </div>      
                            
                           
                        
                        </div>
                      </div>

        </TabContainer>} */}


            {this.state.open === true ? <React.Fragment>
              {/* <Snackbar msg={this.state.message || 'Done'} open="true" /> */}
               </React.Fragment> : ' '}






          </div>
        </div>
      </React.Fragment>
    );
  }
}
