import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import updatePerson from './store/actions/personActions';
import updateGame from './store/actions/gameActions';
import fatchUser from './store/actions/userActions';
import { connect } from 'react-redux';
import Dashboard from './container/dashboard';
import SalaryDashboard from './container/payslip-dashboard';

import Login from './container/site/login';
import Register from './container/site/register';
import ActivateUser from './container/site/activateUser';

import ActivateAdmin from './container/site/activateAdmin';

import UploadDocuments from './container/uploadDocuments';
import JobDetails from './container/jobdetails';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

import { createBrowserHistory } from "history";

const history = createBrowserHistory();


const theme = createMuiTheme({
  props: {
    MuiTextField: {
      variant: 'outlined',
      size: 'small',
    },

    MuiSelectField: {
      variant: 'outlined',
    },
  },
  overrides: {
    MuiIconButton: {
      sizeSmall: {
        marginLeft: 4,
        marginRight: 4,
        padding: 16,
      },
    },
  },
});

function App(props) {
  return (
    <ThemeProvider theme={theme}>
      <React.Fragment>
        <Router history={history} basename={'/hdfc/'}>
          <Switch>
            <Route exact path="/" component={Login} />
            <Route exact path="/register" component={Register} />
            <Route exact path="/dashboard" component={Dashboard} />
            <Route exact path="/activate_customer" component={ActivateUser} />
            <Route exact path="/activate_admin" component={ActivateAdmin} />
            <Route exact path="/upload-documents" component={UploadDocuments} />
            <Route exact path="/job-details/:id" component={JobDetails} />
            <Route exact path="/salary-slip-dashboard" component={SalaryDashboard} />
          </Switch>
        </Router>
      </React.Fragment>
    </ThemeProvider>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateGame: () => {
      dispatch(updateGame);
    },
    updatePerson: () => {
      dispatch(updatePerson);
    },
    fatchUser: () => {
      dispatch(fatchUser);
    },
  };
};

const mapStateToProps = (state) => {
  return {
    game: state.game,
    person: state.person,
    users: state.users,
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(App);
