/* eslint-disable require-jsdoc */
import React, { Component } from 'react';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox, { CheckboxProps } from '@material-ui/core/Checkbox';
import LinkItem from '../../../components/linkItem';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import LinearProgress from '@material-ui/core/LinearProgress';
import Button from '@material-ui/core/Button';
import Loader from '../../../components/loader';
import Snackbar from '../../../components/snackbar';
import axios from "axios";
import { withTheme } from 'react-jsonschema-form';
import { Theme as MuiTheme } from 'rjsf-material-ui';
import Logo from '../../../images/ind-logo.png';
import { Progress } from 'reactstrap';
import { ToastContainer, toast } from 'react-toastify';

const Form = withTheme(MuiTheme);
const schema = require('./schema.json');
const uiSchema = require('./ui-schema.json');
const formData = require('./formData.json');

class login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      schema: schema,
      uiSchema: uiSchema,
      formData: formData,
      loading: false,
      getLoansLoader: false,
      open: false
    };
  }

  onChange = (value) => {
    console.log('value', value)
  };

  onBeforeChange = (value) => {
    const { onChange } = this.props;
    console.log('value', value)
  };

  onSubmit = (value) => {
    console.log('onSubmit:', value.formData)
    let data = value.formData
    console.log('onSubmit data:', data)
    this.registerUser(data)
  }

  async registerUser(data) {
    this.setState({ getLoansLoader: true });

    try {
      const res = await axios.post(process.env.react_app_base_url + '/credit/user_login', data);
      const resp = res.data;
      console.log("resp", resp)
      if (res.status === 200) {
        sessionStorage.setItem('reactToken', res.data.token);
        sessionStorage.setItem('name', res.data.firstname);
        const data = { 'token': res.data.token };
        this.getBankName(data);
        this.setState({ getLoansLoader: false, open: true });
        this.props.history.push('/dashboard');
      } else {
        this.setState({ getLoansLoader: false, open: true, message: resp.message });
        // setTimeout(
        //   function () {
        //     window.location.reload(false);
        //   }
        //     .bind(this),
        //   2000,
        // );
      }
    } catch (error) {

      if (error.message === 'Network Error') {
        alert(JSON.stringify(error.message));
        this.setState({ open: true, message: error.message });
        // setTimeout(
        //   function () {
        //     window.location.reload(false);
        //   }
        //     .bind(this),
        //   2000,
        // );
      } else {
        console.log("error", JSON.stringify(error))
        alert('Something went wrong please try again');
        // setTimeout(
        //   function () {
        //     window.location.reload(false);
        //   }
        //     .bind(this),
        //   2000,
        // );
      }

    }
  };

  onCancel = () => {
    const { data } = this.props;
    this.setState({
      ...data,
    });
  }

  onChangeCheckbox = (e) => {
    const values = e.target.checked;

    console.log("Onchange Value", values);
  }

  async componentDidMount() {
    this.setState({ loading: true });
    sessionStorage.removeItem('reactToken')
    sessionStorage.removeItem('name')
  };

  async getBankName(data) {
    this.setState({ getLoansLoader: true });

    try {
      const res = await axios.post(process.env.react_app_base_url + '/credit/bank_name_list', data);
      const resp = res.data;
      console.log('resp', resp.bank_names_list);
      const bank_names_list = resp.bank_names_list;
      this.setState({ bankNameList: bank_names_list });
      sessionStorage.setItem('bank_names_list',JSON.stringify(bank_names_list))
      if (res.status === 200) {

        this.setState({ getLoansLoader: false, open: true });

      } else if (res.status === 201) {


        if (res.data.message == 'Token is invalid' || res.data.message == 'Not Authorized' || res.data.message == 'Token is missing!') {

          toast.error(res.data.message);
          setTimeout(
            function () {
              this.props.history.push('/');
            }
              .bind(this),
            2000,
          );

        }

      }
      else {




        setTimeout(
          function () {
            window.location.reload(false);
          }
            .bind(this),
          2000,
        );


      }
    } catch (error) {

      toast.error('Network Error');
      if (error.message === 'Network Error') {
        alert(JSON.stringify(error.message));
        this.setState({ open: true, message: error.message });
        setTimeout(
          function () {
            window.location.reload(false);
          }
            .bind(this),
          2000,
        );
      } else {
        console.log("error", JSON.stringify(error))
        alert('Something went wrong please try again');
        setTimeout(
          function () {
            window.location.reload(false);
          }
            .bind(this),
          2000,
        );
      }

    }
  };

  render() {
    const { schema, uiSchema, formData } = this.state;
    return (
      <React.Fragment>
        <div className="page" id="site-login">

          <div className="logo">
            <img alt={'IN-D Credit'} src={Logo} ></img>
          </div>

          <div className="login-body">



            {this.state.loading === true ? <Form
              schema={this.state.schema}
              uiSchema={uiSchema}
              formData={formData}
              onSubmit={this.onSubmit}
              onChange={this.onFormChanged}
            >
              {this.state.getLoansLoader === false ? '' : <React.Fragment> <div className="loginLoader"> <LinearProgress />  </div> </React.Fragment>}
              <div className="navbarSteps">
                <div className="row justify-content-end">
                  <div className="col-md-12">

                    <Button variant="contained" color="primary" type="submit" disabled={this.state.getLoansLoader === true ? true : false} > Sign In  </Button>


                    <div className="loginCheckbox">

                      <FormControlLabel
                        control={
                          <Checkbox
                            icon={<CheckBoxOutlineBlankIcon fontSize="small" />}
                            checkedIcon={<CheckBoxIcon fontSize="small" />}
                            value="checkedI"
                            color="primary"
                            value="Yes"
                            onChange={this.onChangeCheckbox}

                          />
                        }
                        label="Keep me signed-in"
                      />

                    </div>


                  </div>
                </div>
              </div>
            </Form> : <Loader msg={"Please wait..."} />}




            <div className="loginCheckbox">
              <p className="lineDivider">
                <span>New to IN-D?</span>

              </p>


              <LinkItem to={'/register'} variant="contained" title={'Create your IN-D Account'}> </LinkItem>
            </div>

            {this.state.open === true ? <React.Fragment><Snackbar msg={this.state.message || 'Done'} open="true" /> </React.Fragment> : ' '}


          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default login;